<script>
import axios from "axios";
import FileDragDrop from "./FileDragDrop";

//const url = "http://localhost:3000/users";
const url = "https://api-dev.chsli.org:8000/users";

export default {
  components: {
    FileDragDrop
  },
  data: () => ({
    ref: {},
    refFiles: [],
    menuDOB: false,
    menuEncounterDate: false,
    //    formState: null,
    dialog: false,
    snackbar: {
      show: false,
      color: "",
      message: ""
    }
  }),
  mounted() {
    this.ref = this.initializeRef();
    this.$refs.form.reset();
  },
  watch: {
    menuDOB(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = "YEAR"));
    }
  },
  methods: {
    saveDOB(date) {
      this.$refs.menuDOB.save(date);
      // clear errors if needed.  vee-validate has an issue with disabled fields
      const field = this.$validator.fields.find({ name: "Date of Birth" });
      field.reset();
      this.$validator.errors.remove(field.name, field.scope);
    },
    updateFileList(obj) {
      console.log("Updating parent file listing...");
      this.refFiles = obj;
    },
    cancelForm() {
      this.dialog = false;
      this.ref = this.initializeRef();
    },
    submitForm() {
      this.$validator.validate().then(async valid => {
        if (!valid) {
          this.$el
            .querySelector(
              '[name="' + this.$validator.errors.items[0].field + '"]'
            )
            .scrollIntoView();
          window.scrollBy(0, -120); // adjust scrolling higher
          return;
        }

        let formData = new FormData();

        formData.set("ptFirstName", this.ref.ptFirstName);
        formData.set("ptMiddleInitial", this.ref.ptMiddleInitial);
        formData.set("ptLastName", this.ref.ptLastName);
        formData.set("ptAddress", this.ref.ptAddress);
        formData.set("ptCity", this.ref.ptCity);
        formData.set("ptZip", this.ref.ptZip);
        formData.set("ptPhone", this.ref.ptPhone);
        formData.set("ptDOB", this.ref.ptDOB);
        formData.set("ptGender", this.ref.ptGender);
        formData.set("ptInsurance1", this.ref.ptInsurance1);
        formData.set("ptPolicy1", this.ref.ptPolicy1);
        formData.set("ptInsurance2", this.ref.ptInsurance2);
        formData.set("ptPolicy2", this.ref.ptPolicy2);
        formData.set("ptDiagnosis1", this.ref.ptDiagnosis1);
        formData.set("ptDiagnosis2", this.ref.ptDiagnosis2);
        formData.set("ptMedicalHistory", this.ref.ptMedicalHistory);
        formData.set("ptReferralType", this.ref.ptReferralType);
        formData.set("drEncounterDate", this.ref.drEncounterDate);
        formData.set("drMedicalFindings", this.ref.drMedicalFindings);
        formData.set("drFirstName", this.ref.drFirstName);
        formData.set("drLastName", this.ref.drLastName);
        formData.set("drAddress", this.ref.drAddress);
        formData.set("drCity", this.ref.drCity);
        formData.set("drZip", this.ref.drZip);
        formData.set("drPhone", this.ref.drPhone);
        formData.set("drEmail", this.ref.drEmail);
        formData.set("drLicense", this.ref.drLicense);
        formData.set("drNPI", this.ref.drNPI);
        formData.set("drReferralAgree", this.ref.drReferralAgree);
        formData.set("drCertifyIllness", this.ref.drCertifyIllness);
        formData.set("drPatientAgree", this.ref.drPatientAgree);

        for (var i = 0; i < this.refFiles.length; i++) {
          formData.append("files[" + i + "]", this.refFiles[i]);
        }

        axios
          .post(url, formData)
          .then(response => {
            this.snackbar.color = "green";
            this.snackbar.message =
              "Your referral has been successfully submitted.  Thank you.";
            this.snackbar.show = true;

            this.$refs.form.reset();
            this.ref = this.initializeRef();
            this.$refs.fileDragDrop.reset(); // reset the child component
          })
          .catch(error => {
            console.log(error);
            this.snackbar.color = "red";
            this.snackbar.message =
              "We're sorry, we're not able to send this information at the moment, please try again later.  " +
              error;
            this.snackbar.show = true;
          });

        // try {
        //   await axios.post(url, this.ref);

        //   this.snackbar.color = "green";
        //   this.snackbar.message =
        //     "Your referral has been successfully submitted.  Thank you.";
        //   this.snackbar.show = true;

        //   // this.$refs.form.reset();
        //   this.ref = this.initializeRef();
        // } catch (e) {
        //   this.snackbar.color = "red";
        //   this.snackbar.message =
        //     "We're sorry, we're not able to send this information at the moment, please try again later.  " +
        //     e;
        //   this.snackbar.show = true;
        // }
      });
    },
    initializeRef() {
      return {
        ptFirstName: "",
        ptMiddleInitial: "",
        ptLastName: "",
        ptAddress: "",
        ptCity: "",
        ptZip: "",
        ptPhone: "",
        ptDOB: "",
        ptGender: "",
        ptInsurance1: "",
        ptPolicy1: "",
        ptInsurance2: "",
        ptPolicy2: "",
        ptDiagnosis1: "",
        ptDiagnosis2: "",
        ptMedicalHistory: "",
        ptReferralType: "",
        drEncounterDate: "",
        drMedicalFindings: "",
        drFirstName: "",
        drLastName: "",
        drAddress: "",
        drCity: "",
        drZip: "",
        drPhone: "",
        drEmail: "",
        drLicense: "",
        drNPI: "",
        drReferralAgree: "",
        drCertifyIllness: "",
        drPatientAgree: ""
      };
    },
    initializeRefForWade() {
      return {
        ptFirstName: "zzzz",
        ptMiddleInitial: "zzzz",
        ptLastName: "zzzz",
        ptAddress: "zzzz",
        ptCity: "zzzz",
        ptZip: "33333",
        ptPhone: "",
        ptDOB: "06/13/1990",
        ptGender: "",
        ptInsurance1: "zzzz",
        ptPolicy1: "zzzz",
        ptInsurance2: "",
        ptPolicy2: "",
        ptDiagnosis1: "",
        ptDiagnosis2: "",
        ptMedicalHistory: "",
        ptReferralType: "",
        drEncounterDate: "",
        drMedicalFindings: "",
        drFirstName: "zzzz",
        drLastName: "zzzz",
        drAddress: "zzzz",
        drCity: "zzzz",
        drZip: "12311",
        drPhone: "",
        drEmail: "",
        drLicense: "",
        drNPI: "",
        drReferralAgree: "yes",
        drCertifyIllness: "yes",
        drPatientAgree: "yes"
      };
    }
  }
};
</script>

